.carousel-scene {
  width: 500px;
  height: 350px;
  margin: 40px auto;
  perspective: 1000px;
}

.carousel {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 1s ease;
  cursor: grab;
}

.carousel:active {
  cursor: grabbing;
  transition: none;
}

.carousel__cell {
  position: absolute;
  width: 300px;
  height: 300px;
  left: calc(50% - 150px);
  top: calc(50% - 150px);
  backface-visibility: hidden;
}
.carousel__cell img {
  width: 100%;
  height: 100%;
  object-fit: cover;       /* ‚úÖ Crops the image to fill the box */
  background: none;        /* ‚úÖ No white background */
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}


/* Optional auto‚Äërotate pause on hover */
.carousel-scene:hover .carousel {
  animation-play-state: paused;
}
/* üåü Glowing animated orbs in the background */
.carousel-scene::before,
.carousel-scene::after {
  content: "";
  position: absolute;
  width: 600px;
  height: 600px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 182, 193, 0.2), transparent 70%);
  animation: pulseGlow 6s ease-in-out infinite alternate;
  z-index: 0;
  pointer-events: none;
}

.carousel-scene::after {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(255, 105, 180, 0.15), transparent 70%);
  animation-delay: 3s;
}

/* ‚ú® Subtle pulse animation */
@keyframes pulseGlow {
  0% {
    transform: translate(-50%, -50%) scale(0.95);
