  filter: none !important;
}

/* ‚îÄ‚îÄ Image Styling ‚îÄ‚îÄ */
/* ‚îÄ‚îÄ Default: no border ‚îÄ‚îÄ */
.carousel__cell img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);

  /* REMOVE any default border */
  border: none;

  /* smooth transitions */
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease,
    border 0.3s ease;
}

/* ‚îÄ‚îÄ Hover: add border + glow ‚îÄ‚îÄ */
.carousel__cell:hover img {
  transform: scale(1.05);

  /* bright neon glow */
  box-shadow:
